.cat-info {
  flex-direction: column;
  align-items: center;
  display: block;
}

.cat-info img {
  max-width: 300px;
  margin-top: 10px;
}

.cat-info h2, .cat-info p {
  margin-top: 10px;
  font-family: Roboto, sans-serif;
}

.cat-info h2 {
  font-size: 1.5rem;
}

.cat-info p {
  width: 300px;
  font-size: 1rem;
}

.loader {
  height: 50px;
  width: 50px;
  animation: rotate 1s infinite;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader:before {
  background-color: #fff;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #ff3d00;
}

.loader:after {
  background-color: #ff3d00;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 #fff;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #ff3d00;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #ff3d00;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #ff3d00;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 #fff;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #fff;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #fff;
  }
}

